require( './styles/index.scss' );


window.addEventListener( "load", ( ) => {
      const loaderText = document.querySelector( ".loader__text" );
      const loader = document.querySelector( ".loader" );
      const loaderSpinner = document.querySelector( ".loader__spinner" );

      loaderText.classList.add( "loader__text--finish" );
      loaderSpinner.classList.add( "loader--finish" );
      window.setTimeout(()=>{
            loader.classList.add( "loader--finish2" );
      },1500);
} );

const logo = ( ) => {
      const main = document.querySelector( '.main__section' );
      const image = document.querySelector( '.logo__background' );

      if ( document.documentElement.scrollTop || window.pageYOffset > main.offsetTop ) {
            image.style.transform = `translateY(${(document.documentElement.scrollTop - main.offsetTop)/1.2}px)`;

      } else {
            image.style.transform = `translateY(0px)`;
      }
}

const navbar = ( ) => {
      const nav = document.querySelector( '.navigation' );
      const btn = document.querySelector( '.btn--login' );

      if ( document.documentElement.scrollTop || document.body.scrollTop > window.innerHeight ) {
            nav.classList.add( "navigation--white" );
            btn.classList.remove( "btn--white" );
            btn.classList.add( "btn--red" );
      } else {
            nav.classList.remove( "navigation--white" );
            btn.classList.remove( "btn--red" );
            btn.classList.add( "btn--white" );
      }
}

const fade = ( elem ) => {
      const el = document.querySelector( elem );
      if ( document.documentElement.scrollTop > ( ( el.getBoundingClientRect( ).top + document.documentElement.scrollTop ) - ( 85 / 100 * window.innerHeight ) ) ) {
            el.classList.remove( "fade__up" );
            el.classList.add( "fade__in" );
      }
}


window.addEventListener( "scroll", function ( e ) {
      window.requestAnimationFrame( ( ) => {
            navbar( );
            logo( );
            fade( "#workflow" );
            fade( "#jala__dashboard" );
            fade( "#jala__assistant" );
      } )
} );



const burger = document.querySelector( "#hamburger" );
const animate = ( ) => {
      const circle = document.querySelector( ".navigation__label-icon .circle" );
      const line1 = document.querySelector( ".navigation__label-icon .line-1" );
      const line2 = document.querySelector( ".navigation__label-icon .line-2" );
      const labelIcon = document.querySelector( ".navigation__label-icon" );
      
      if ( burger.dataset.asu === "0" ) {
            circle.classList.add( "circle-in" );
            line1.classList.add( "line1-in" );
            line2.classList.add( "line2-in" );
            labelIcon.style.backgroundColor = "#FDFFFC";
            burger.dataset.asu = "1"
      } else if ( burger.dataset.asu === "1" ) {
            circle.classList.remove( "circle-in" );
            line1.classList.remove( "line1-in" );
            line2.classList.remove( "line2-in" );
            labelIcon.style.backgroundColor = "#2B2D42";
            burger.dataset.asu = "0"
      }
}

burger.addEventListener( 'click', animate );


const featureDash = document.getElementById("jala__dashboard-image");
const featureAst = document.getElementById("jala__assistant-image");


featureDash.addEventListener("click", el => {
      const id = el.target.closest(".features__item").dataset.dash;

      if( id ) {
            const itemAnim = document.querySelector(`[data-dash="${id}"]`);
            const image = document.querySelector(`.features__image--dashboard--${id}`);
            const imageActive = document.querySelector(".features__image--dashboard--active");
            const itemActive = document.querySelector("#jala__dashboard .features__item--active");

            itemActive.classList.remove("features__item--active");
            itemAnim.classList.add("features__item--active");

            imageActive.classList.remove("features__image--dashboard--active")
            image.classList.add("features__image--dashboard--active");
      }
})

featureAst.addEventListener("click", el => {
      const id = el.target.closest(".features__item").dataset.asst;

      if( id ) {
            const image = document.querySelector(`.features__image--2--${id}`);
            const imageActive = document.querySelector(".features__image--2--active");
            const itemAnim = document.querySelector(`[data-asst="${id}"]`);
            const itemActive = document.querySelector("#jala__assistant .features__item--active");

            itemActive.classList.remove("features__item--active");
            itemAnim.classList.add("features__item--active");

            imageActive.classList.remove("features__image--2--active");
            image.classList.add("features__image--2--active");
      }
})